import { useContext, useState, useTransition, type ReactElement } from "react";
import { WalletsCtx } from "./walletsCtx";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import {
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@mui/material";
import { useForm, type SubmitHandler } from "react-hook-form";
import type { Wallet } from "./walletsData";
// import bs58 from "bs58"; // 

export function WalletDlg({
  initAddress = "",
  initName = "",
  type = "add",
  children,
}: {
  initAddress?: string;
  initName?: string;
  type?: "add" | "modify";
  children: (props: { triggerOpen: () => void }) => ReactElement;
}) {
  const [open, setOpen] = useState(false);
  const { dispatch } = useContext(WalletsCtx);
  const [isPending, startTransition] = useTransition();
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm<Wallet>({
    defaultValues: {
      address: initAddress,
      name: initName,
    },
  });

  const title = type === "add" ? "" : "";

  function handleOpen() {
    reset({
      address: initAddress,
      name: initName,
    });
    setOpen(true);
  }

  const onSubmit: SubmitHandler<Wallet> = (data) => {
    // 
    setOpen(false);

    //  startTransition 
    startTransition(() => {
      const wallet = {
        address: data.address,
        name: data.name,
      };

      // UI
      queueMicrotask(() => {
        dispatch({ type, wallet });
      });
    });
  };

  function validateAddress() {
    // try {
    //   const decoded = bs58.decode(value);
    //   if (decoded.length !== 32) {
    //     return " 32  Base58 ";
    //   }
    // } catch (e) {
    //   return ": " + e;
    // }
    return true;
  }

  return (
    <>
      {children({ triggerOpen: handleOpen })}
      <Dialog open={open} onClose={() => setOpen(false)}>
        <DialogTitle id="alert-dialog-title">{title}</DialogTitle>
        <DialogContent sx={{ paddingBottom: 0 }}>
          <form onSubmit={handleSubmit(onSubmit)} style={{ paddingTop: 8 }}>
            <TextField
              sx={{ marginBottom: 2 }}
              fullWidth
              label=""
              disabled={type === "modify"}
              {...register("address", {
                required: "",
                validate: validateAddress,
              })}
              error={!!errors.address}
              helperText={errors.address && errors.address.message}
            />
            <TextField
              label=""
              fullWidth
              {...register("name", { required: "" })}
              error={!!errors.name}
              helperText={errors.name && errors.name.message}
            />
            <DialogActions>
              <Button onClick={() => setOpen(false)}></Button>
              <Button type="submit" disabled={isPending}>
                {isPending ? "..." : ""}
              </Button>
            </DialogActions>
          </form>
        </DialogContent>
      </Dialog>
    </>
  );
}
